import file from './filezreo'

export default (time)=>{
    let d = new Date();
    d.setTime(time);
    let y = d.getFullYear();
    let m = d.getMonth()+1;
    let n =d.getDate();

    let h =d.getHours();
    let mi = d.getMinutes();
    let s = d.getSeconds();
    // return y+''+file(m)+''+file(n)+''+' '+ h+':'+mi+':'+s
    return `${y}${file(m)}${file(n)} ${file(h)}:${file(mi)}:${file(s)}`;
}